"use client";
import { useState, useEffect } from 'react';
import axios from 'axios';
import Header from './component/Header';
import Inputs from './component/Inputs';
import Buttons from './component/Buttons';
import UserList from './component/List';

const API_URL = "http://localhost:3001/users";

type User = {
    id: number;
    name: string;
    email: string;
};

export default function Home() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [userList, setUserList] = useState<User[]>([]);

  const fetchUsers = async () => {
    try {
      const response = await axios.get(API_URL);
      setUserList(response.data); 
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const handleSubmit = async () => {
        if (name.trim() === '') {
            alert("Please enter a name.");
            return;
        }

        const newUser = { name: name, email: email };

        try {
            await axios.post(API_URL, newUser);
            fetchUsers(); 
            setName('');
            setEmail('');
        } catch (error) {
            alert("Failed to save.");
            console.error(error);
        }
    };

  const handleDelete = async (index: number) => {
        const idToDelete = userList[index].id;
        try {
            await axios.delete(`${API_URL}/${idToDelete}`);
            setUserList(prevList => prevList.filter((_, i) => i !== index));
        } catch (error) {
            alert("Failed to delete.");
        }
    };

    const handleEdit = async (index: number) => {
        const currentUser = userList[index];
        const idToEdit = currentUser.id; 
        
        const newName = prompt("Edit Name:", currentUser.name);
        const newEmail = prompt("Edit Email:", currentUser.email);

        if (newName !== null && newEmail !== null) {
            const updatedUser = { name: newName, email: newEmail };
            try {
                await axios.put(`${API_URL}/${idToEdit}`, updatedUser);
                fetchUsers();
            } catch (error) {
                alert("Failed to update.");
            }
        }
    }

  return (
      <div className="flex items-center justify-center min-h-screen bg-black flex-col gap-4">
        <div className="w-64 bg-white text-black p-4 rounded-xl shadow-lg">
          <div className="flex flex-col items-center justify-center h-full">
            <Header title="User Form"/>
            <Inputs name={name} email={email} setName={setName} setEmail={setEmail}/>
            <Buttons name="Submit" onClick={handleSubmit}/>
          </div>
        </div>

        <div className="w-64 bg-gray-900 text-white p-4 rounded-xl shadow-lg border border-gray-700">
             <h3 className="text-lg font-bold m-2 mb-2">Submitted Users:</h3>
             <UserList users={userList} onDelete={handleDelete} onEdit={handleEdit}/>
        </div>
      </div>
  );
}